import { LightHttp, LightJsonUtil, LightPop, LightLoading } from './../../../light/light-out/light-out.module';
import { Component, OnDestroy } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Keyboard } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-email-detail',
  templateUrl: 'email-detail.html',
})
export class EmailDetailPage implements OnDestroy{

  code: any;

  //通知标题
  emailTitle: any = {};

  //通知内容
  Content: any;

  //回复内容的值
  replyContent: string = "";

  // 附件
  attachList: Array<any>;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightHttp: LightHttp,
    public lightPop: LightPop,
    public lightLoading: LightLoading,
    public keyboard: Keyboard,
  ) {
    this.code = navParams.data.item.CODE;
  }

  public ngOnInit(): void {
    this.getEmailDetail();
    // 无效的写法，只在进入页面时打印
    this.keyboard.onClose(() => {
      console.log('keyboard close');
    })
    // 有效的键盘隐藏时打印写法
    window.addEventListener('keyboardWillHide',this.testKeyBoardEvent)
  }
  
  public ngOnDestroy(): void {
    // 组件销毁时移除监听
    window.removeEventListener('keyboardWillHide', this.testKeyBoardEvent);
    console.log('new detail destroy')
  }

  // 隐藏键盘时触发的回调函数
  testKeyBoardEvent() {
    console.log('keyboard close2');
  }

  /**
   *获取邮件标题、内容、附件
   *
   * @memberof EmailDetailPage
   */
  async getEmailDetail() {
    // 启用等待的遮罩层
    this.lightLoading.presentLoading('', () => {
      console.log('new detail loading ok')
    })
    // 同时发起请求
    const [newTitle, Content, attachList] = await Promise.all([
      this.lightHttp.get("Email/GetEmailInfo", { objectID: this.code }),
      this.lightHttp.get("Email/GetEmailContent", { objectID: this.code }),
      this.lightHttp.get("Attachment/GetAttachments", { objectID: this.code }),
    ])
    // 等三个请求的数据全部返回后再渲染页面
    this.emailTitle = LightJsonUtil.toObject(newTitle);
    this.Content = Content;
    this.attachList = LightJsonUtil.toObject(attachList) as Array<any>;
    // 关闭遮罩层
    this.lightLoading.stopLoading()
  }

  //回复邮箱
  reply() {
    this.replyContent = this.replyContent;
    this.lightHttp.get("Email/ReplyEmail", { objectID: this.code, content: this.replyContent })
      .then((data) => {
        this.lightPop.confirm(data);
        this.navCtrl.push("EmailListPage");
      })
  }

}



